<template lang="html">
  <div class="_full_router userInfo">
    <custHeader :title="title"></custHeader>
    <div class="userInfoBody">
      <div class="userDiv">
        <a>
          <div class="userImg">
            <!-- <img class="userImg" src="" alt=""> -->
            <i slot="icon" class="fa fa-user" style="    color: #999;"></i>
          </div>
          
          <span>{{ mb }}</span>
        </a>

      </div>
      <div class="list">
        <mt-cell title="账户" :value="account"></mt-cell>
        <div class="" @click="setPro">
          <!-- <mt-cell title="实名认证"  is-link to="/realnameAuth"></mt-cell> -->
          <mt-cell title="实名认证" :value=authStatus></mt-cell>
        </div>
        <!-- <mt-cell title="身份认证" ></mt-cell> -->

        <mt-cell :title="proTitle" is-link to="/personal/setProblem"></mt-cell>


        <!-- <mt-cell title="风险测评" is-link to="/riskAnalysis" style="margin-bottom:6px;"></mt-cell> -->
        <!-- <mt-cell title="指纹登陆"><mt-switch v-model="fingerprint"></mt-switch></mt-cell> -->

        <mt-cell title="更换手机号码" is-link to="/personal/changeMb"></mt-cell>
        <mt-cell title="修改登录密码" is-link to="/personal/modifiyPass"></mt-cell>
        <!-- <mt-cell title="开启手势面目"><mt-switch v-model="gesture"></mt-switch></mt-cell>
        <mt-cell title="修改手势面目" is-link></mt-cell> -->
        <!-- <mt-cell title="修改交易密码" is-link></mt-cell> -->
        <mt-cell title="重置交易密码" is-link  to="/findTrdPassword" style="margin-bottom:6px;"></mt-cell>
        <!-- <mt-cell title="首次设置交易密码"is-link ></mt-cell> -->
      </div>
    </div>
  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import {MessageBox,Cell,Switch} from 'mint-ui'
import cookieUtil from 'js-cookie'
Vue.component(Cell.name,Cell);
Vue.component(Switch.name,Switch);
export default {
  data(){
    return {
      account:'',
      title:"账户中心",
      fingerprint:false,
      gesture:false,
      authStatus:'',
      mb:'',
      proTitle:'设置安保问题'
    }
  },
  created:function(){
    var self = this;
    self.mb = cookieUtil.get("mb");

    axiosUtil.qrySafeQuestion().then(data => {
      var data = data.data;
      if(data.data !== undefined){
        self.proTitle = "重置安保问题"
      }
    })


      axiosUtil.getRealnameAuthQry().then(data=>{
       var data=data.data;
      // 0;//0未//1已//2未通过//4审核中
       if(data.errorcode==0){
          self.account = data.data.investorSname
           if(data.data.authStatus==0){
               self.authStatus="未认证";
           }else if(data.data.authStatus==1){
              self.authStatus="已认证";
           }else if(data.data.authStatus==2){
              self.authStatus="未通过";
           }else if(data.data.authStatus==4){
              self.authStatus="认证中";
           }
       }else{
         self.authStatus="未认证";
       }
     })

  },
  methods:{
    setPro:function(){
      var self = this;
      axiosUtil.qrySafeQuestion().then(data => {
        var data = data.data;
        if(data.data !== undefined){
          if(self.authStatus == "认证中"){
            router.push("/personal/infoProduce")
          }else if(self.authStatus == "未认证" || self.authStatus == "未通过"){
            router.push("/personal/realnameAuth")
          }

        }else{
          MessageBox.confirm("没有设置安全问题？请先去设置安全问题").then(action => {
              router.push("/personal/setProblem")
          })
        }
      })
    }
  },
 components:{
    custHeader
  },
}
</script>

<style lang="css">
.userInfo{
    background-color: #eee;
    z-index: 5;
    position: fixed;
    width: 100%;
    top: 0;
}

.userInfoBody{
  overflow: scroll;
  height: 165vmin;
}
.userDiv{
  height: 60px;
  background-color: #fff;
  margin-bottom: 9px;
  line-height: 60px;
}
.userImg{
  float: left;
  width: 50px;
  height: 50px;
  margin-left: 20px;
  margin-top: 5px;
  margin-right: 40%;
  font-size: 32px;
}
</style>



